<template>
    <div class="container">
      <div :class="{ 'sidebarContainer': true, 'visible': sidebarVisible, 'hidden': !sidebarVisible }" :style="{ backgroundColor: themeColor }"></div>
      <div :class="{ 'sidebarContainer_float': true, 'visible': sidebarVisible, 'hidden': !sidebarVisible }" :style="{ backgroundColor: themeColor }">
        <SidebarComp :themeColor="themeColor" />
      </div>

      <button @click="toggleSidebar" :class="{ 'sidebarButton': true, 'fold': sidebarVisible, 'open': !sidebarVisible }" :style="{ backgroundColor: themeColor }">
        <img :src="sidebarVisible ? '/image/chevron_compactLeft.png' : '/image/chevron_compactRight.png'" alt="chevron" class="sidebarButton_img"/>
      </button>

      <div :class="{ 'mainContainer': true, 'normal': sidebarVisible, 'extended': !sidebarVisible }">
        <router-view></router-view>
      </div>

      <button @click="toggleLoginBox" :class="{ 'loginBoxButton': true, 'fold': loginBoxVisible, 'open': !loginBoxVisible }">
        <img :src="loginBoxVisible ? '/image/chevron_compactDown.png' : '/image/chevron_compactUp.png'" alt="chevron" class="loginBoxButton_img"/>
      </button>

      <div :class="{ 'loginBox': true, 'fold': loginBoxVisible, 'open': !loginBoxVisible }" :style="{ backgroundColor: themeColor }">
        <LoginComp />
      </div>

    </div>
</template>

<script>
import { ref } from 'vue';
import SidebarComp from './components/SidebarComp.vue';
import LoginComp from './components/LoginComp.vue';
import { mapState } from 'vuex';

export default {
  name: 'App',
  components: {
    SidebarComp,
    LoginComp,
  },
  computed: {
    ...mapState('themeColorStore', ['themeColor'])
  },
  methods: {
    changeColor(color) {
      this.$store.commit('themeColorStore/changeColor',color);
      console.log(this.$store.state.themeColorStore.themeColor);
    }
  },
  setup() {
    const sidebarVisible = ref(true);
    const loginBoxVisible = ref(true);

    const toggleSidebar = () => {
      sidebarVisible.value = !sidebarVisible.value;
    };

    const toggleLoginBox = () => {
      loginBoxVisible.value = !loginBoxVisible.value;
    };

    return {
      sidebarVisible,
      loginBoxVisible,
      toggleSidebar,
      toggleLoginBox,
    };
  },
};
</script>

<style src="./App.css"> </style>
