<template>
    <div class="headerDiv" :style="{ backgroundColor: headerColor }">
      <div class="blank" />
      <div class="contentContainer">
        <div class="titleContainer">{{ title }}</div>
        <hr class="dividerHr"/>
        <div v-if="!hideBtm" class="tabContainer">
          <button v-for="(content, index) in contents" @click="() => $emit('content-change', index)" :key="index" :class="{ 'headerButton': true, 'selected': currentIndex === index }">
            {{ content.tab }}
          </button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { mapState } from 'vuex';

  export default {
    name: 'HeaderComp',

    props: {
      title: String,
      'content-change': Function,
      contents: Array,
      currentIndex: Number,
      hideBtm: Boolean,
    },

    computed: {
      ...mapState('themeColorStore', ['themeColor']),
      headerColor() {
        switch(this.themeColor) {
          case 'Gray':
            return '#D9D9D9';
          case 'DarkSlateGray':
            return '#9ea39e';
          case 'lightslategray':
            return '#c9c8c1';
          default:
            return '#D9D9D9';
        }
      }
    },
    methods: {
      changeColor(color) {
        console.log(color);
        this.$store.commit('themeColorStore/changeColor',color);
        console.log(this.$store.state.themeColorStore.themeColor);
      }
    },
  };
  </script>
  
  <style src="./CSS/Header.css"> </style>
  