<template>
    <div class="exp">
      <ExpComp
        :image="getImage(item)"
        :headText="getHeadText(item)"
        :mainText="getMainText(item)"
      />
    </div>
  </template>
  
  <script>
  import ExpComp from './ExpComp.vue';
  
  export default {
    name: 'ExperienceList',
    components: {
      ExpComp,
    },
    props: {
      item: String,
    },
    methods: {
      getImage(item) {
        return item === 'iOS' ? 'expImage1.png' : item === 'Node.js' ? 'expImage2.png' : '';
      },
      getHeadText(item) {
        return item === 'iOS'
          ? 'UIKit과 SwiftUI를 통한 iOS 앱 개발의 전반적인 방법을 공부하고 있습니다.'
          : item === 'Node.js'
          ? 'Node.js를 통한 백엔드 개발의 전반적인 방법을 공부하고 있습니다.'
          : '';
      },
      getMainText(item) {
        return item === 'iOS'
          ? `<div class="paragraph">
                  Apple Human Interface Guideline 정리 <br />
                  Realm, Alamofire, Lottie 등의 라이브러리 사용. <br />
                  앱-서버 통신 수행(Alamofire,Moya) <br />
                  UIView와 CoreAnimation을 활용한 여러 UI 애니메이션 작업  <br />
                  + 커스텀 애니메이션 작업(Lottie, CoreAnimation) <br />
                  앱 데이터베이스 연결(Coredata, Realm) <br />
                  DispatchQueue를 사용한 비동기 코드 처리 <br />
                  RxSwift, MVVM을 통한 테스팅 지향 프로그래밍 <br />
              </div>`
          : item === 'Node.js'
          ? `<div class="paragraph">
                  기본 Node를 사용한 서버 구축 <br />
                  MongoDB/MySQL 연결한 웹DB서버 구축 <br />
                  Express, MySQL, EJS 사용한 MVC 모델 구축 <br />
                  AWS 연동, 무중단 배포 경험 <br />
                  JWT 토큰을 통한 보안 API 연결 <br />
              </div>`
          : '';
      },
    },
  };
  </script>
  