<template>
    <div>
      <template v-if="loginState">
        <div class="login_flex">
          <div>
            <label>Welcome!</label>
            <div class="login_container">
              <button @click="changeColor('Gray')" id="grayBtn"></button>
              <button @click="changeColor('DarkSlateGray')" id="navyBtn"></button>
              <button @click="changeColor('lightslategray')" id="blackBtn"></button>
            </div>
          </div>
          <button @click="handleLogout()" class="login_button">Log Out</button>
        </div>
      </template>
      <template v-else>
        <div class="login_flex">
          <div class="login_container vertical">
            <div class="login_container">
              <label>ID:</label>
              <input
                type="text"
                v-model="username"
                class="login_input"
              />
            </div>
            <div class="login_container">
              <label>PW:</label>
              <input
                type="password"
                v-model="password"
                class="login_input"
              />
            </div>
          </div>
          <button @click="handleLogin(username,password)" class="login_button">Log In</button>
        </div>
      </template>
    </div>
  </template>
  
  <script>
  import { ref } from 'vue';
  import { mapState } from 'vuex';
  
  export default {
    name: 'LoginComp',
  
    computed: {
      ...mapState('themeColorStore', ['themeColor']),
      ...mapState('loginStore', ['loginState'])
    },
    methods: {
      changeColor(color) {
        this.$store.commit('themeColorStore/changeColor',color);
      },
      changeLoginState(Bool) {
        this.$store.commit('loginStore/changeLoginState',Bool);
      },
      handleLogin(ID,PW){
            if (ID === '1234' && PW === '1234') {
                this.changeLoginState(true);
              } else {
                alert('아이디 또는 비밀번호가 올바르지 않습니다.');
              }
        },
        handleLogout() {
            this.changeLoginState(false);
        }
    },
    setup() {
      const username = ref('');
      const password = ref('');
  
      return {
        username,
        password
      };
    },
  };
  </script>
  
  <style src="./CSS/Login.css"> </style>
  