<template>
    <div id="contest">
      <HeaderComp :title="title" @contentChange="contentChange" :currentIndex="index" :contents="experiences" />
      <component :is="experiences[index].content" v-if="experiences[index].content" :item="experiences[index].item"/>
    </div>
  </template>
  
  <script>
  import { ref } from 'vue';
  import experiences from './Data/experience';
  import HeaderComp from './HeaderComp.vue';
  
  export default {
    name: 'ExperiencePage',
    components: {
      HeaderComp,
    },
    setup() {
      const index = ref(0);
  
      const contentChange = (newIndex) => {
        index.value = newIndex;
      };
  
      return {
        title: 'Experience',
        index,
        experiences,
        contentChange,
      };
    },
  };
  </script>
  
  <style src="./CSS/Experience.css"> </style>
  