<template>
    <div class="introContainer">
      <HeaderComp :title="title" @contentChange="contentChange" :currentIndex="index" :contents="intros" :hideBtm="true"/>
      <component :is="intros[index].content" v-if="intros[index].content" />
    </div>
  </template>
  
  <script>
  import { ref } from 'vue';
  import intros from './Data/introduction';
  import HeaderComp from './HeaderComp.vue';
  
  export default {
    name: 'IntroductionPage',
    components: {
      HeaderComp,
    },
    setup() {
      const index = ref(0);
  
      const contentChange = (newIndex) => {
        index.value = newIndex;
      };
  
      return {
        title: 'Introduction',
        index,
        intros,
        contentChange,
      };
    },
  };
  </script>
  
  <style src="./CSS/Introduction.css"> </style>
  