<template>
    <div id="project">
      <HeaderComp :title="title" @contentChange="contentChange" :currentIndex="index" :contents="projects" />
      <component :is="projects[index].content" v-if="projects[index].content" />
    </div>
  </template>
  
  <script>
  import { ref } from 'vue';
  import projects from './Data/projects';
  import HeaderComp from './HeaderComp.vue';
  
  export default {
    name: 'ProjectPage',
    components: {
      HeaderComp,
    },
    setup() {
      const index = ref(0);
  
      const contentChange = (newIndex) => {
        index.value = newIndex;
      };
  
      return {
        title: 'Project',
        index,
        projects,
        contentChange,
      };
    },
  };
  </script>
  
  <style src="./CSS/Project.css"> </style>
  